.blog-container {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    gap: 5vh;
    align-items: center;
    padding-bottom: 10vh;
}

.blog {
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.247);
    border-radius: 10px;
    width: 80%;
    height: 50vh;
    position: relative;
}

img {
    height: 50%;
    object-fit: cover;
}

.btn{
    background: none;
    border: none;
    cursor: pointer;
    border-radius: 10px;
    background-color: rgb(169, 0, 169);
    color: white;
    font-weight: 700;
    padding: 5px;
    font-size: medium;
}

.btn:active{
    background-color: rgba(169, 0, 169, 0.392);
}

.likes{
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
    gap: 5px;
    align-items: center;
}